import { keelungList } from '@/assets/list/keelungList'
if (process.browser) { // Here we introduce... According to the environment wow.js
  var { WOW } = require('wowjs')
}

import VanillaTilt from 'vanilla-tilt';

export default {
  head: {
    title: '關於 - 西打藍 接案陪跑 / 商業行銷顧問',
    meta: [
      { name: 'keywords', content: '西打藍, 西打藍官方網站, 自由工作者, 自由接案, 前端工程師, 文字工作者, 網站開發, 網站架設, 軟體工程師' },
    ]
  },
  layout: 'default',
  components: {
    boxContent: require('~/components/common/box-content.vue').default,
  },
  props: {

  },
  data() {
    return {
      keelungImg: keelungList,
      swiperOption: {
        slidesPerView: 1,
        spaceBetween: 30,
        // loop: true,
        // lazy: true,
        // lazy: {
        //   loadPrevNext: true
        // },
        // loadOnTransitionStart: true,
        // watchSlidesVisibility: true, //解决了swiper前面的一个不能提前加载的bug
        autoplay: {
          disableOnInteraction: false
        },
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      }
    }
  },
  mounted() {
    // scroll down then play
    if (!!window.IntersectionObserver) {
      let observer = new IntersectionObserver(
        (entries, observer) => {
          entries.forEach((entry) => {
            this.swiper.autoplay.stop()
            if (entry.isIntersecting) {
              // console.log(entry)
              this.swiper.autoplay.start()

              // You can remove the observer if you do not need it
              observer.unobserve(entry.target)
            }
          });
        },
        { rootMargin: "0px 0px -200px 0px" }
      );

      // Add the observer to you swiper
      observer.observe(document.querySelector(".about-keelung"))
    } else {
      // You can use the polyfill or just start the autoplay
    }


    // tilt
    // https://micku7zu.github.io/vanilla-tilt.js/
    const element = document.querySelector(".js-tilt");
    VanillaTilt.init(element, {
      max: 25,
      speed: 400
    });

    // gsap
    this.$gsap.fromTo('.about-intro-head', { opacity: 0 }, { opacity: 1, x: -60, duration: 2 })

    // wow
    this.$nextTick(() => {
      if (process.browser) { // On the page mounted In the life cycle Instantiate according to the environment WOW
        new WOW({
          boxClass: 'wow', // 欲套用wow.js的class
          animateClass: 'animated', // 欲修改設定animat.css 的類別名稱
          offset: 0, // 當用戶滾動並到達這個距離時才開始執行動畫
          mobile: true, // 是否在行動裝置上執行動畫
          live: false, // 非同步產生的內容是否要套用
          callback: null,
          scrollContainer: null
        }).init()
      }
    })
  },
  computed: {
    swiper() { return this.$refs.mySwiper.$swiper },
  },
}
